<template>
    <div class="notice-page">
        <div class="container">
            <!-- 헤더 섹션 -->
            <div class="notice-header">
                <h1 class="page-title">공지사항</h1>
            </div>

            <!-- 검색 및 필터 -->
            <div class="notice-controls">
                <div class="search-box">
                    <input 
                        type="text" 
                        v-model="searchKeyword" 
                        placeholder="제목 또는 내용을 검색하세요..."
                        class="search-input"
                    >
                </div>
                <div class="filter-box">
                    <select v-model="selectedCategory" class="filter-select">
                        <option value="">전체</option>
                        <option value="general">일반</option>
                        <option value="important">중요</option>
                        <option value="update">업데이트</option>
                    </select>
                </div>
            </div>

            <!-- 공지사항 리스트 -->
            <div class="notice-list">
                <div 
                    v-for="notice in paginatedNotices" 
                    :key="notice.id"
                    class="notice-item"
                    @click="viewNotice(notice)"
                >
                    <div class="notice-badge-wrapper">
                        <span 
                            v-if="notice.category === 'important'" 
                            class="notice-badge important"
                        >
                            중요
                        </span>
                        <span 
                            v-else-if="notice.isNew" 
                            class="notice-badge new"
                        >
                            NEW
                        </span>
                    </div>
                    
                    <div class="notice-content">
                        <h3 class="notice-title">{{ notice.title }}</h3>
                        <p class="notice-summary">{{ notice.summary }}</p>
                        <div class="notice-meta">
                            <span class="notice-date">{{ formatDate(notice.createdAt) }}</span>
                            <span class="notice-views">조회 {{ notice.views }}</span>
                        </div>
                    </div>
                    
                    <div class="notice-arrow">
                        <span class="arrow-icon">></span>
                    </div>
                </div>

                <!-- 빈 상태 -->
                <div v-if="filteredNotices.length === 0" class="empty-state">
                    <p class="empty-message">검색 결과가 없습니다</p>
                </div>
            </div>

            <!-- 개선된 페이지네이션 -->
            <div class="pagination" v-if="totalPages > 1">
                <!-- 이전 버튼 -->
                <button 
                    @click="goToPage(currentPage - 1)"
                    :disabled="currentPage === 1"
                    class="page-btn nav-btn"
                >
                    ‹ 이전
                </button>
                
                <!-- 첫 페이지 -->
                <button 
                    v-if="startPage > 1"
                    @click="goToPage(1)"
                    class="page-btn"
                >
                    1
                </button>
                
                <!-- 생략 표시 -->
                <span v-if="startPage > 2" class="page-dots">...</span>
                
                <!-- 페이지 번호들 -->
                <button 
                    v-for="page in visiblePages" 
                    :key="page"
                    @click="goToPage(page)"
                    :class="['page-btn', { active: currentPage === page }]"
                >
                    {{ page }}
                </button>
                
                <!-- 생략 표시 -->
                <span v-if="endPage < totalPages - 1" class="page-dots">...</span>
                
                <!-- 마지막 페이지 -->
                <button 
                    v-if="endPage < totalPages"
                    @click="goToPage(totalPages)"
                    class="page-btn"
                >
                    {{ totalPages }}
                </button>
                
                <!-- 다음 버튼 -->
                <button 
                    @click="goToPage(currentPage + 1)"
                    :disabled="currentPage === totalPages"
                    class="page-btn nav-btn"
                >
                    다음 ›
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Notice',
    data() {
        return {
            searchKeyword: '',
            selectedCategory: '',
            currentPage: 1,
            itemsPerPage: 5, // 한 페이지에 5개만 표시
            maxVisiblePages: 5, // 한번에 보여줄 페이지 수
            notices: [
                {
                    id: 1,
                    title: '새로운 기능이 추가되었습니다',
                    summary: '사용자 경험을 개선하는 새로운 기능들이 추가되었습니다. 자세한 내용을 확인해보세요.',
                    category: 'important',
                    createdAt: '2025-06-15',
                    views: 152,
                    isNew: true
                },
                {
                    id: 2,
                    title: '와 주말 ㅈㄴ빨리가네ㅡㅡ',
                    summary: '뿌에에에에에엥엥',
                    category: 'update',
                    createdAt: '2025-06-16',
                    views: 4321,
                    isNew: true
                },
                {
                    id: 3,
                    title: '서비스 업데이트 완료',
                    summary: 'v2.1.0 업데이트가 완료되었습니다. 새로운 기능과 개선사항을 확인하세요.',
                    category: 'update',
                    createdAt: '2025-06-13',
                    views: 234,
                    isNew: false
                },
                {
                    id: 4,
                    title: '서비스 업데이트 완료',
                    summary: 'v2.0.0 업데이트가 완료되었습니다. 새로운 기능과 개선사항을 확인하세요.',
                    category: 'update',
                    createdAt: '2025-04-16',
                    views: 1420,
                    isNew: false
                },
                {
                    id: 5,
                    title: '정기 점검 안내',
                    summary: '시스템 안정성 향상을 위한 정기 점검이 예정되어 있습니다.',
                    category: 'general',
                    createdAt: '2025-02-14',
                    views: 89,
                    isNew: false
                },
                {
                    id: 6,
                    title: '보안 업데이트 공지',
                    summary: '보안 강화를 위한 시스템 업데이트가 진행되었습니다.',
                    category: 'important',
                    createdAt: '2025-01-20',
                    views: 567,
                    isNew: false
                },
                {
                    id: 7,
                    title: '사용자 가이드 업데이트',
                    summary: '더 나은 사용자 경험을 위해 가이드가 업데이트되었습니다.',
                    category: 'general',
                    createdAt: '2025-01-15',
                    views: 123,
                    isNew: false
                },
                {
                    id: 8,
                    title: '신규 서비스 출시 안내',
                    summary: '새로운 서비스가 곧 출시될 예정입니다. 많은 관심 부탁드립니다.',
                    category: 'important',
                    createdAt: '2025-01-10',
                    views: 789,
                    isNew: false
                }
            ]
        }
    },
    computed: {
        filteredNotices() {
            let filtered = this.notices;
            
            if (this.searchKeyword) {
                filtered = filtered.filter(notice => 
                    notice.title.toLowerCase().includes(this.searchKeyword.toLowerCase()) ||
                    notice.summary.toLowerCase().includes(this.searchKeyword.toLowerCase())
                );
            }
            
            if (this.selectedCategory) {
                filtered = filtered.filter(notice => notice.category === this.selectedCategory);
            }
            
            return filtered;
        },
        totalPages() {
            return Math.ceil(this.filteredNotices.length / this.itemsPerPage);
        },
        paginatedNotices() {
            const start = (this.currentPage - 1) * this.itemsPerPage;
            const end = start + this.itemsPerPage;
            return this.filteredNotices.slice(start, end);
        },
        startPage() {
            const half = Math.floor(this.maxVisiblePages / 2);
            let start = this.currentPage - half;
            
            if (start < 1) start = 1;
            if (start + this.maxVisiblePages - 1 > this.totalPages) {
                start = this.totalPages - this.maxVisiblePages + 1;
            }
            if (start < 1) start = 1;
            
            return start;
        },
        endPage() {
            let end = this.startPage + this.maxVisiblePages - 1;
            if (end > this.totalPages) end = this.totalPages;
            return end;
        },
        visiblePages() {
            const pages = [];
            for (let i = this.startPage; i <= this.endPage; i++) {
                pages.push(i);
            }
            return pages;
        }
    },
    methods: {
        viewNotice(notice) {
            console.log('공지사항 보기:', notice);
        },
        formatDate(dateString) {
            const date = new Date(dateString);
            return `${date.getFullYear()}.${String(date.getMonth() + 1).padStart(2, '0')}.${String(date.getDate()).padStart(2, '0')}`;
        },
        goToPage(page) {
            if (page >= 1 && page <= this.totalPages) {
                this.currentPage = page;
            }
        }
    }
}
</script>

<style scoped>
.notice-page {
    min-height: 100vh;
    background: #f8f9fa;
    padding: 2rem 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
}

/* 헤더 */
.notice-header {
    text-align: center;
    margin-bottom: 3rem;
    margin-top: 60px;
    padding: 2rem 0;
}

.page-title {
    font-size: 2.5rem;
    font-weight: 300;
    color: #212529;
    margin-bottom: 0.5rem;
    letter-spacing: -0.02em;
}

/* 컨트롤 영역 */
.notice-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    gap: 1rem;
}

.search-box {
    flex: 1;
    max-width: 400px;
}

.search-input {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    font-size: 0.95rem;
    background: #ffffff;
    transition: all 0.2s ease;
}

.search-input:focus {
    outline: none;
    border-color: #6c757d;
    box-shadow: 0 0 0 3px rgba(108, 117, 125, 0.1);
}

.filter-box {
    min-width: 150px;
}

.filter-select {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    background: #ffffff;
    font-size: 0.95rem;
    cursor: pointer;
    transition: border-color 0.2s ease;
}

.filter-select:focus {
    outline: none;
    border-color: #6c757d;
}

/* 공지사항 리스트 */
.notice-list {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    overflow: hidden;
    margin-bottom: 2rem;
}

.notice-item {
    display: flex;
    align-items: center;
    padding: 1.5rem;
    border-bottom: 1px solid #f1f3f4;
    cursor: pointer;
    transition: all 0.2s ease;
    position: relative;
}

.notice-item:last-child {
    border-bottom: none;
}

.notice-item:hover {
    background: #f8f9fa;
}

.notice-badge-wrapper {
    margin-right: 1rem;
    min-width: 60px;
}

.notice-badge {
    display: inline-block;
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    font-size: 0.75rem;
    font-weight: 500;
    text-align: center;
}

.notice-badge.important {
    background: #dc3545;
    color: white;
}

.notice-badge.new {
    background: #28a745;
    color: white;
}

.notice-content {
    flex: 1;
}

.notice-title {
    font-size: 1.1rem;
    font-weight: 500;
    color: #212529;
    margin: 0 0 0.5rem 0;
    line-height: 1.4;
}

.notice-summary {
    font-size: 0.9rem;
    color: #6c757d;
    margin: 0 0 0.75rem 0;
    line-height: 1.5;
}

.notice-meta {
    display: flex;
    gap: 1rem;
    font-size: 0.85rem;
    color: #adb5bd;
}

.notice-arrow {
    margin-left: 1rem;
    color: #adb5bd;
    transition: transform 0.2s ease;
}

.notice-item:hover .notice-arrow {
    transform: translateX(4px);
    color: #6c757d;
}

.arrow-icon {
    font-size: 1.2rem;
}

/* 빈 상태 */
.empty-state {
    text-align: center;
    padding: 3rem;
    color: #adb5bd;
}

.empty-message {
    font-size: 1rem;
    margin: 0;
}

/* 개선된 페이지네이션 스타일 */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.25rem;
    margin-top: 1rem;
}

.page-btn {
    padding: 0.5rem 0.75rem;
    border: 1px solid #e9ecef;
    background: #ffffff;
    color: #6c757d;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.9rem;
    min-width: 40px;
}

.page-btn:hover:not(:disabled) {
    background: #f8f9fa;
    border-color: #6c757d;
}

.page-btn.active {
    background: #6c757d;
    color: white;
    border-color: #6c757d;
}

.page-btn:disabled {
    background: #f8f9fa;
    color: #adb5bd;
    cursor: not-allowed;
    border-color: #e9ecef;
}

.nav-btn {
    padding: 0.5rem 1rem;
    font-weight: 500;
}

.page-dots {
    color: #6c757d;
    padding: 0.5rem 0.25rem;
    font-size: 0.9rem;
}

/* 반응형 */
@media (max-width: 768px) {
    .notice-page {
        padding: 1rem 0;
    }
    
    .page-title {
        font-size: 2rem;
    }
    
    .notice-controls {
        flex-direction: column;
        align-items: stretch;
    }
    
    .search-box {
        max-width: none;
    }
    
    .notice-item {
        flex-direction: column;
        align-items: flex-start;
        text-align: left;
    }
    
    .notice-badge-wrapper {
        margin-right: 0;
        margin-bottom: 0.5rem;
        min-width: auto;
    }
    
    .notice-arrow {
        align-self: flex-end;
        margin-left: 0;
        margin-top: 0.5rem;
    }
    
    .notice-meta {
        flex-direction: column;
        gap: 0.25rem;
    }
    
    .pagination {
        gap: 0.15rem;
    }
    
    .page-btn {
        padding: 0.4rem 0.6rem;
        font-size: 0.85rem;
        min-width: 35px;
    }
    
    .nav-btn {
        padding: 0.4rem 0.8rem;
    }
}

/* 접근성 */
.notice-item:focus {
    outline: 2px solid #6c757d;
    outline-offset: -2px;
}

.search-input::placeholder {
    color: #adb5bd;
}

/* 로딩 상태 */
.notice-item.loading {
    opacity: 0.6;
    pointer-events: none;
}
</style>
